@use "vars";
@use "utils";

ul.constraints-list {
    &, ul {
        padding: 0;
        list-style-type: none;
    }

    .constraint-row {
        @include utils.clearfix;

        white-space: nowrap;

        .constraint-row-left {
            float: left;
        }
        .constraint-row-right {
            margin-left: 0.5em;
            float: right;

            .constraint-iconbutton {
                margin-left: 0.5em;
                display: inline-block;
            }
        }
    }

    .section-title {
        margin: 0.5em 0;
        font-weight: vars.$font-weight-heavy;

        white-space: nowrap;
    }

    form > div {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}
